.header {
    display: flex;
    justify-content: space-between;
    padding: 20px;
    background-color: #333;
    color: #fff;
  }
  
  .header nav a {
    color: white;
    text-decoration: none;
    margin-left: 20px;
  }
  
  .header nav a:hover {
    text-decoration: underline;
  }